<template>
  <div class="container">
    <div id="extraction">
      <h3>Text Extraction</h3>
      <h5>How to use?</h5>
      <p>
        Key in the unprocessed text and the text that surrounds the required
        text.
      </p>
      <div class="input-group mb-3 unprocessed-text">
        <div class="input-group-prepend">
          <span class="input-group-text">Unprocessed text</span>
        </div>
        <textarea class="form-control" v-model="unprocessedText"></textarea>
      </div>
      <div class="input-group mb-3 surrounding-text">
        <div class="input-group-prepend">
          <span class="input-group-text">Surrounding text</span>
        </div>
        <input type="text" class="form-control" v-model="surroundingText" />
      </div>
      <div class="input-group mb-3 processed-text">
        <div class="input-group-prepend">
          <span class="input-group-text">Processed text</span>
        </div>
        <input
          type="text"
          class="form-control"
          ref="processedText"
          v-model="processedText"
        />
      </div>
      <button @click="copy" type="button" class="btn btn-primary">COPY!</button>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      unprocessedText: "",
      surroundingText: "&quot;",
      processedText: "",
    };
  },
  methods: {
    copy() {
      this.selectText(this.$refs.processedText); // e.g. <div ref="text">
      document.execCommand("copy");
    },
    selectText(element) {
      var range;
      if (document.selection) {
        // IE
        range = document.body.createTextRange();
        range.moveToElementText(element);
        range.select();
      } else if (window.getSelection) {
        range = document.createRange();
        range.selectNode(element);
        window.getSelection().removeAllRanges();
        window.getSelection().addRange(range);
      }
    },
  },
  watch: {
    unprocessedText: function () {
      const splitedText = this.unprocessedText.split(this.surroundingText);
      this.processedText = splitedText[1];
    },
  },
};
</script>

<style scoped>
.surrounding-text {
  width: 30rem;
}
.input-group-prepend {
  width: 200px;
}
</style>